<template>
  <div class="container">
    <p>You have successfully been logged in.</p>
    <button class="continue-btn"> Continue <v-icon class="white--text">arrow_forward</v-icon> </button>
  </div>
</template>

<script>
export default {
  name: 'GoogleCallback',
  data () {
    return {}
  },
  mounted () {
    console.log('hey guys I just got called')
    console.log(this.$route.query.code)
    this.$http.get('http://localhost:3000/v1/auth/google/callback', {
      code: this.$route.query.code
    }).then(response => {
      console.log(response)
    }).catch(response => {
      console.log(response)
    })
  }
}
</script>

<style>
  .container {
    padding: 40px 0
  }
  .continue-btn {
    padding: 20px;
    background: rgba(16, 16, 235, 0.534);
    border-radius: 3px;
    width: 200px;
    color: white;
    font-size: 18px;
  }
</style>



